@import url(https://fonts.googleapis.com/css?family=Ubuntu);
body {
  margin: 1;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
    "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(31, 34, 32);
  background: linear-gradient(325deg, rgba(31, 34, 32, 1) 0%, rgba(74, 109, 105, 1) 86%, rgba(34, 228, 85, 1) 100%);
  min-height: 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size: 200% 200%;
  -webkit-animation: movingGradient 5s linear infinite alternate;
          animation: movingGradient 5s linear infinite alternate;
  /* height: 200vh; */
  /* width: 100vw; */
}
@-webkit-keyframes movingGradient {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 100% 100%;
  }
}
@keyframes movingGradient {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 100% 100%;
  }
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

h4 {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

